import * as React from 'react';
import { IntlProvider } from 'react-intl';
import { locales } from '~i18n/i18n';
import useLocale from '~context/Hooks/useLocale';
import { LocaleContextInterface } from '~types/intl';
import { messages as msg } from '~i18n/messages';

const defaultLangContext: LocaleContextInterface = {
	activeLocale: null,
	setLocale: () => null,
	locales: null,
};

export const LocaleContext = React.createContext<LocaleContextInterface>(
	defaultLangContext,
);

export default function LocaleWrapper({ pageLocale, messages, children }) {
	const [locale, setLocale] = useLocale(pageLocale);
	const ssrMessages = messages ? messages : msg[locale];
	return (
		<LocaleContext.Provider
			value={{
				activeLocale: locale,
				setLocale,
				locales,
			}}
		>
			<IntlProvider locale={locale} messages={ssrMessages}>
				{children}
			</IntlProvider>
		</LocaleContext.Provider>
	);
}
