import * as React from 'react';
import LocaleWrapper from './Context/LocaleContext';
import ThemeWrapper from './Context/ThemeContext';
//import FramerWrapper from './Context/FramerContext';

//import Helper from './Helper/Helper';
//import '../styles/main.css';
import '../styles/styles.scss';
import { getMessages } from '~i18n/messages';

export const Layout: React.FC<{
	pageLocale: string;
	messages: Record<string, string>;
}> = ({ pageLocale, messages, children }) => {
	const messagesSSR =
		process.env.NODE_ENV == 'development' ? getMessages(pageLocale) : messages;

	return (
		<>
			<LocaleWrapper pageLocale={pageLocale} messages={messagesSSR}>
				<ThemeWrapper>
					{/* <FramerWrapper> */}
					{children}
					{/* </FramerWrapper> */}
				</ThemeWrapper>
			</LocaleWrapper>
		</>
	);
};

export default Layout;
